// src/layouts/AppLayout.tsx
import React, { useState } from "react";
import { Link, Outlet, useLocation } from "react-router-dom";
import { useAuth } from "../context/AuthContext";

const AppLayout: React.FC = () => {
  const { user, logout } = useAuth();
  const location = useLocation();
  const [isMobileNavOpen, setIsMobileNavOpen] = useState(false);

  const isActive = (path: string): boolean =>
    location.pathname.startsWith(path);

  const handleLogout = async () => {
    await logout();
    setIsMobileNavOpen(false);
  };

  const handleNavigate = () => {
    setIsMobileNavOpen(false);
  };

  const NavLinks: React.FC<{ onNavigate?: () => void }> = ({ onNavigate }) => (
    <nav className="app-sidebar-nav">
      <Link
        to="/app/dashboard"
        className={isActive("/app/dashboard") || location.pathname === "/app" ? "active" : undefined}
        onClick={onNavigate}
      >
        Dashboard
      </Link>
      <Link
        to="/app/bots"
        className={isActive("/app/bots") ? "active" : undefined}
        onClick={onNavigate}
      >
        Bots
      </Link>
      <Link
        to="/app/billing"
        className={isActive("/app/billing") ? "active" : undefined}
        onClick={onNavigate}
      >
        Billing
      </Link>
      <Link
        to="/app/account"
        className={isActive("/account") ? "active" : undefined}
        onClick={onNavigate}
      >
        Profile
      </Link>
    </nav>
  );

  return (
    <div className="app-layout-root">
      {/* DESKTOP SIDEBAR */}
      <aside className="app-sidebar">
        <div className="app-sidebar-header">
          <h2>Dashboard</h2>
        </div>
        <NavLinks />
      </aside>

      <div className="app-main">
        {/* TOP BAR (desktop + mobile) */}
        <header className="app-main-header">
          <div className="app-main-header-left">
            {/* Burger only visible on mobile via CSS */}
            <button
              type="button"
              className="app-burger"
              onClick={() => setIsMobileNavOpen(true)}
              aria-label="Open navigation"
            >
              <span className="app-burger-icon" />
            </button>
            <span className="brand-small">Coslo - Assistente AI</span>
          </div>

          {/* Desktop-only user info (hidden on mobile via CSS) */}
          <div className="app-main-header-right">
            {user && (
                <button className="btn-link" onClick={logout}>
                  Logout
                </button>
            )}
          </div>
        </header>

        {/* MOBILE SLIDE-IN NAV */}
        {isMobileNavOpen && (
          <div
            className="app-mobile-nav-overlay"
            onClick={() => setIsMobileNavOpen(false)}
          >
            <aside
              className="app-mobile-nav"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="app-mobile-nav-header">
                <span className="brand-small">Dashboard</span>
                <button
                  type="button"
                  className="app-mobile-nav-close"
                  onClick={() => setIsMobileNavOpen(false)}
                  aria-label="Close navigation"
                >
                  
                </button>
              </div>

              <NavLinks onNavigate={handleNavigate} />

              {user && (
                <div className="app-mobile-nav-user">
                  <div className="app-mobile-nav-email">{user.email}</div>
                  <button
                    type="button"
                    className="btn-link danger"
                    onClick={handleLogout}
                  >
                    Logout
                  </button>
                </div>
              )}
            </aside>
          </div>
        )}

        <main className="app-main-content">
          <Outlet />
        </main>
      </div>
    </div>
  );
};

export default AppLayout;
