// src/api/bots.ts
import { authFetchJson } from "./authorizedClient";

export type BotStatus =
  | "DRAFT"
  | "PENDING_PAYMENT"
  | "ACTIVE"
  | "SUSPENDED"
  | "CANCELED";

export interface Bot {
  id: string;
  userId: string;
  slug: string;
  name: string;
  description?: string | null;
  systemPrompt: string;
  knowledgeClientId?: string | null;
  domain?: string | null;
  useDomainCrawler: boolean;
  usePdfCrawler: boolean;
  channelWeb: boolean;
  channelWhatsapp: boolean;
  channelInstagram: boolean;
  channelMessenger: boolean;
  useCalendar: boolean;
  status: BotStatus;
  createdAt: string;
  updatedAt: string;
  calendarId?: string | null;
  timeZone?: string | null;
  defaultDurationMinutes?: number | null;
}

export type ChannelType = "WEB" | "WHATSAPP" | "FACEBOOK" | "INSTAGRAM";

export interface BotChannel {
  id: string;
  botId: string;
  type: ChannelType;
  externalId: string;
  accessToken: string;
  meta?: any;
  createdAt: string;
  updatedAt: string;
}

export interface Conversation {
  id: string;
  botId: string;
  channel: ChannelType;
  externalUserId: string;
  lastMessageAt: string;
  createdAt: string;
  updatedAt: string;
}

export type MessageRole = "USER" | "ASSISTANT" | "SYSTEM";

export interface ConversationMessage {
  id: string;
  conversationId: string;
  role: MessageRole;
  content: string;
  channelMessageId?: string | null;
  createdAt: string;
}

export interface CheckoutResponse {
  checkoutUrl: string;
}

// --- Pricing preview types (features only) ---

export type FeatureCode =
  | "DOMAIN_CRAWLER"
  | "PDF_CRAWLER"
  | "CHANNEL_WEB"
  | "WHATSAPP"
  | "MESSENGER"
  | "INSTAGRAM"
  | "CALENDAR";

export interface BotPricingLineItem {
  code: FeatureCode;
  label: string;
  monthlyAmountCents: number;
  monthlyAmountFormatted: string;
  currency: string;
}

export interface BotPricingPreview {
  lineItems: BotPricingLineItem[];
  totalAmountCents: number;
  totalAmountFormatted: string;
  currency: string;
}

export interface BotPricingPreviewPayload {
  useDomainCrawler?: boolean;
  usePdfCrawler?: boolean;
  channelWeb?: boolean;
  channelWhatsapp?: boolean;
  channelMessenger?: boolean;
  channelInstagram?: boolean;
  useCalendar?: boolean;
}

// --- Usage plans (for tokens/limits) ---

export interface UsagePlan {
  id: string;
  code: string;
  name: string;
  description?: string | null;
  monthlyTokens?: number | null;
  monthlyAmountCents: number;
  currency: string;
  createdAt: string;
  updatedAt: string;
}

// ---- Bots ----

export async function fetchBots(): Promise<Bot[]> {
  return authFetchJson<Bot[]>("/bots");
}

export interface CreateBotPayload {
  name: string;
  slug: string;
  description?: string;

  systemPrompt?: string;
  domain?: string;

  useDomainCrawler?: boolean;
  usePdfCrawler?: boolean;
  channelWeb?: boolean;
  channelWhatsapp?: boolean;
  channelInstagram?: boolean;
  channelMessenger?: boolean;
  useCalendar?: boolean;

  calendarId?: string | null;
  timeZone?: string | null;
  defaultDurationMinutes?: number | null;
}

export async function createBot(payload: CreateBotPayload): Promise<Bot> {
  return authFetchJson<Bot>("/bots", {
    method: "POST",
    body: JSON.stringify(payload)
  });
}

export interface UpdateBotPayload {
  name?: string;
  description?: string;
  systemPrompt?: string;
  domain?: string | null;
  useDomainCrawler?: boolean;
  usePdfCrawler?: boolean;
  channelWeb?: boolean;
  channelWhatsapp?: boolean;
  channelInstagram?: boolean;
  channelMessenger?: boolean;
  useCalendar?: boolean;
  status?: BotStatus;
  calendarId?: string | null;
  timeZone?: string | null;
  defaultDurationMinutes?: number | null;
}

export async function updateBot(
  id: string,
  payload: UpdateBotPayload
): Promise<Bot> {
  return authFetchJson<Bot>(`/bots/${encodeURIComponent(id)}`, {
    method: "PATCH",
    body: JSON.stringify(payload)
  });
}

export async function deleteBot(id: string): Promise<void> {
  await authFetchJson<unknown>(`/bots/${encodeURIComponent(id)}`, {
    method: "DELETE"
  });
}

export async function getBotById(id: string): Promise<Bot> {
  return authFetchJson<Bot>(`/bots/${encodeURIComponent(id)}`);
}

// ---- Stripe Checkout ----

export async function startBotCheckout(
  id: string,
  payload: { usagePlanId: string }
): Promise<CheckoutResponse> {
  return authFetchJson<CheckoutResponse>(
    `/bots/${encodeURIComponent(id)}/checkout`,
    {
      method: "POST",
      body: JSON.stringify(payload)
    }
  );
}

export async function getBotPricingPreview(
  id: string,
  payload?: BotPricingPreviewPayload
): Promise<BotPricingPreview> {
  return authFetchJson<BotPricingPreview>(
    `/bots/${encodeURIComponent(id)}/pricing-preview`,
    {
      method: "POST",
      body: JSON.stringify(payload || {})
    }
  );
}

export async function cancelBotSubscription(id: string): Promise<Bot> {
  return authFetchJson<Bot>(
    `/bots/${encodeURIComponent(id)}/cancel-subscription`,
    {
      method: "POST"
    }
  );
}

// NEW: fetch usage plans list
export async function fetchUsagePlans(): Promise<UsagePlan[]> {
  return authFetchJson<UsagePlan[]>("/usage-plans");
}

// ---- Knowledge & documents ----

export async function crawlBotDomain(
  botId: string,
  domainOverride?: string
): Promise<{ status: string; knowledgeClientId: string; domain: string }> {
  const body = domainOverride ? { domain: domainOverride } : {};
  return authFetchJson<{ status: string; knowledgeClientId: string; domain: string }>(
    `/bots/${encodeURIComponent(botId)}/knowledge/crawl-domain`,
    {
      method: "POST",
      body: JSON.stringify(body)
    }
  );
}

export async function uploadBotDocuments(
  botId: string,
  files: FileList | File[]
): Promise<{ status: string; knowledgeClientId: string; files: string[] }> {
  const formData = new FormData();
  Array.from(files as FileList | File[]).forEach((file) => {
    formData.append("files", file);
  });

  return authFetchJson<{ status: string; knowledgeClientId: string; files: string[] }>(
    `/bots/${encodeURIComponent(botId)}/knowledge/upload-docs`,
    {
      method: "POST",
      body: formData
    }
  );
}

// ---- Channels ----

export async function fetchChannels(botId: string): Promise<BotChannel[]> {
  return authFetchJson<BotChannel[]>(
    `/bots/${encodeURIComponent(botId)}/channels`
  );
}

export interface CreateChannelPayload {
  type: ChannelType;
  externalId: string;
  accessToken: string;
  meta?: any;
}

export async function createChannel(
  botId: string,
  payload: CreateChannelPayload
): Promise<BotChannel> {
  return authFetchJson<BotChannel>(
    `/bots/${encodeURIComponent(botId)}/channels`,
    {
      method: "POST",
      body: JSON.stringify(payload)
    }
  );
}

export interface UpdateChannelPayload {
  externalId?: string;
  accessToken?: string;
  meta?: any;
}

export async function updateChannel(
  botId: string,
  channelId: string,
  payload: UpdateChannelPayload
): Promise<BotChannel> {
  return authFetchJson<BotChannel>(
    `/bots/${encodeURIComponent(botId)}/channels/${encodeURIComponent(
      channelId
    )}`,
    {
      method: "PATCH",
      body: JSON.stringify(payload)
    }
  );
}

export async function deleteChannel(
  botId: string,
  channelId: string
): Promise<void> {
  await authFetchJson<unknown>(
    `/bots/${encodeURIComponent(botId)}/channels/${encodeURIComponent(
      channelId
    )}`,
    {
      method: "DELETE"
    }
  );
}

