import React, { useEffect, useState } from "react";
import {
  AccountMe,
  fetchAccountMe,
  updateAccountProfile,
  changePasswordApi
} from "../../api/account";

const ProfilePage: React.FC = () => {
  const [account, setAccount] = useState<AccountMe | null>(null);
  const [loading, setLoading] = useState(true);
  const [profileSaving, setProfileSaving] = useState(false);
  const [passwordSaving, setPasswordSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [profileSuccess, setProfileSuccess] = useState<string | null>(null);
  const [passwordSuccess, setPasswordSuccess] = useState<string | null>(null);

  const [name, setName] = useState("");
  const [avatarUrl, setAvatarUrl] = useState("");

  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  useEffect(() => {
    let cancelled = false;
    setLoading(true);
    setError(null);
    fetchAccountMe()
      .then((me) => {
        if (cancelled) return;
        setAccount(me);
        setName(me.name || "");
        setAvatarUrl(me.avatarUrl || "");
      })
      .catch((err) => {
        console.error(err);
        if (!cancelled) {
          setError(err.message || "Failed to load account");
        }
      })
      .finally(() => {
        if (!cancelled) setLoading(false);
      });

    return () => {
      cancelled = true;
    };
  }, []);

  const handleSaveProfile = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!account) return;
    setProfileSaving(true);
    setError(null);
    setProfileSuccess(null);
    try {
      const updated = await updateAccountProfile({
        name: name.trim() || undefined,
        avatarUrl: avatarUrl.trim() || undefined
      });
      setAccount(updated);
      setProfileSuccess("Profile updated.");
    } catch (err: any) {
      console.error(err);
      setError(err.message || "Failed to update profile.");
    } finally {
      setProfileSaving(false);
    }
  };

  const handleChangePassword = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!account || !account.hasPassword) return;
    if (newPassword !== confirmPassword) {
      setError("New password and confirmation do not match.");
      return;
    }

    setPasswordSaving(true);
    setError(null);
    setPasswordSuccess(null);

    try {
      const res = await changePasswordApi(currentPassword, newPassword);
      setPasswordSuccess(res.message || "Password updated.");
      setCurrentPassword("");
      setNewPassword("");
      setConfirmPassword("");
    } catch (err: any) {
      console.error(err);
      setError(err.message || "Failed to change password.");
    } finally {
      setPasswordSaving(false);
    }
  };

  const initials = (() => {
    if (name.trim()) {
      return name
        .trim()
        .split(/\s+/)
        .slice(0, 2)
        .map((n) => n[0])
        .join("")
        .toUpperCase();
    }
    if (account?.email) {
      return account.email[0].toUpperCase();
    }
    return "?";
  })();

  return (
    <div>
      <div className="page-header">
        <div>
          <h1>Account</h1>
          <p className="muted">
            Manage your profile information, login and security.
          </p>
        </div>
      </div>

      {error && <div className="form-error">{error}</div>}
      {profileSuccess && <div className="form-success">{profileSuccess}</div>}
      {passwordSuccess && (
        <div className="form-success">{passwordSuccess}</div>
      )}

      {loading && (
        <div className="detail-main" style={{ marginTop: "1rem" }}>
          <p>Loading account...</p>
        </div>
      )}

      {!loading && account && (
        <>
          {/* Profile card */}
          <section className="detail-main" style={{ marginTop: "1rem" }}>
            <div className="profile-header-row">
              <div className="profile-avatar">
                {avatarUrl ? (
                  <img src={avatarUrl} alt="Avatar" />
                ) : (
                  <span className="profile-avatar-initials">{initials}</span>
                )}
              </div>
              <div>
                <h2 style={{ margin: 0 }}>Profile</h2>
                <p className="muted" style={{ marginTop: "0.25rem" }}>
                  This information is visible inside your dashboard only.
                </p>
              </div>
            </div>

            <form className="form" onSubmit={handleSaveProfile}>
              <div className="form-field">
                <label>Display name</label>
                <input
                  type="text"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  placeholder="Your name or company name"
                />
              </div>

              <div className="form-field">
                <label>Avatar URL</label>
                <input
                  type="url"
                  value={avatarUrl}
                  onChange={(e) => setAvatarUrl(e.target.value)}
                  placeholder="https://"
                />
                <span>
                  You can paste a link to an image (e.g. from your site or
                  logo CDN).
                </span>
              </div>

              <div>
                <button className="btn-primary" type="submit" disabled={profileSaving}>
                  {profileSaving ? "Saving" : "Save profile"}
                </button>
              </div>
            </form>
          </section>

          {/* Login & security */}
          <section className="detail-main" style={{ marginTop: "1.5rem" }}>
            <h2>Login &amp; security</h2>

            <div style={{ marginTop: "0.7rem" }}>
              <p className="muted">
                <strong>Email:</strong> {account.email}
              </p>
              <p className="muted">
                <strong>Login method:</strong>{" "}
                {account.authProvider === "google"
                  ? "Google"
                  : account.hasPassword
                  ? "Email & password"
                  : "Unknown"}
              </p>
            </div>

            {account.hasPassword ? (
              <form
                className="form"
                style={{ marginTop: "1rem" }}
                onSubmit={handleChangePassword}
              >
                <div className="form-field">
                  <label>Current password</label>
                  <input
                    type="password"
                    value={currentPassword}
                    onChange={(e) => setCurrentPassword(e.target.value)}
                  />
                </div>

                <div className="form-field">
                  <label>New password</label>
                  <input
                    type="password"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                  />
                  <span>
                    At least 8 characters, with upper &amp; lower case, a
                    number and a symbol.
                  </span>
                </div>

                <div className="form-field">
                  <label>Confirm new password</label>
                  <input
                    type="password"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                  />
                </div>

                <div>
                  <button
                    className="btn-secondary"
                    type="submit"
                    disabled={passwordSaving}
                  >
                    {passwordSaving ? "Updating" : "Change password"}
                  </button>
                </div>
              </form>
            ) : (
              <p className="muted" style={{ marginTop: "1rem" }}>
                This account uses <strong>Google Sign-In</strong>. Password
                login is not enabled.
              </p>
            )}
          </section>

          {/* 2FA placeholder */}
          <section className="detail-main" style={{ marginTop: "1.5rem" }}>
            <h2>Two-factor authentication</h2>
            <p className="muted" style={{ marginTop: "0.5rem" }}>
              Extra protection for your account using an authenticator app.
            </p>
            <div className="alert-warning" style={{ marginTop: "0.8rem" }}>
              Two-factor authentication is not available yet. We&apos;re
              working on it  in the meantime, choose a strong password and
              keep your email secure.
            </div>
          </section>
        </>
      )}
    </div>
  );
};

export default ProfilePage;
